___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.player-board {
    display: grid;
    height: 400px;
    width: 400px;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(10, 1fr);
}
.cell {
    display: flex;
    background-color: #f4f4f5;
    justify-content: center;
    border: 1px solid black;
    align-items: center;
    font-size: 1.5rem;
    cursor: pointer;
}
.container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 20px;
    height: 100vh;
}
body {
    font-family: 'Roboto', sans-serif;
}
.header-container {
    margin-top: 20px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.missed {
    background-color: #86efac;
}
.hit {
    background-color: #ff7f7f;
}
.board-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
h2{
    margin-bottom: 20px;
    font-size: 2rem;
}
.ship{
    background-color: grey;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;AACA;IACI,aAAa;IACb,aAAa;IACb,YAAY;IACZ,sCAAsC;IACtC,mCAAmC;AACvC;AACA;IACI,aAAa;IACb,yBAAyB;IACzB,uBAAuB;IACvB,uBAAuB;IACvB,mBAAmB;IACnB,iBAAiB;IACjB,eAAe;AACnB;AACA;IACI,aAAa;IACb,6BAA6B;IAC7B,mBAAmB;IACnB,gBAAgB;IAChB,aAAa;AACjB;AACA;IACI,iCAAiC;AACrC;AACA;IACI,gBAAgB;IAChB,WAAW;IACX,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;AAC1B;AACA;IACI,yBAAyB;AAC7B;AACA;IACI,yBAAyB;AAC7B;AACA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;AAC1B;AACA;IACI,mBAAmB;IACnB,eAAe;AACnB;AACA;IACI,sBAAsB;AAC1B","sourcesContent":["* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n.player-board {\n    display: grid;\n    height: 400px;\n    width: 400px;\n    grid-template-columns: repeat(10, 1fr);\n    grid-template-rows: repeat(10, 1fr);\n}\n.cell {\n    display: flex;\n    background-color: #f4f4f5;\n    justify-content: center;\n    border: 1px solid black;\n    align-items: center;\n    font-size: 1.5rem;\n    cursor: pointer;\n}\n.container {\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n    margin-top: 20px;\n    height: 100vh;\n}\nbody {\n    font-family: 'Roboto', sans-serif;\n}\n.header-container {\n    margin-top: 20px;\n    width: 100%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    flex-direction: column;\n}\n.missed {\n    background-color: #86efac;\n}\n.hit {\n    background-color: #ff7f7f;\n}\n.board-container{\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    flex-direction: column;\n}\nh2{\n    margin-bottom: 20px;\n    font-size: 2rem;\n}\n.ship{\n    background-color: grey;\n}"],"sourceRoot":""}]);
